 	onLoad() {
		this.tryAPP=[1,2,3]
 		if (!this.hasLogin) {
			uni.showLoading({
			    title: '加载中'
			});
			this.$forceUpdate();
			
			setTimeout(function () {
			    uni.hideLoading();
			}, 2000);
			let validUser = service.getUsers();
			if (validUser.length !== 0) {
				// 存在缓存数据getUsers（）对象
				console.log(validUser[0].token)
				const newData = {
					token:validUser[0].token
				}
				uni.request({  
					url:BASE_URL + "api/v1/Index/indexData",  
					data: newData,  
					method:'GET',  
					dataType:'json',  
					header:{  
						'content-type':'application/json'  
					},
					success: (e) =>{  
						console.log(e) 
						 if (e.statusCode === 200) {
						 	if (e.data.code === 200) {
						 		var myData = e.data.data
						 		this.menus = {
						 			all_income: myData.all_income,
						 			status: myData.status,
						 			limit: myData.limit,
						 			today_order_num: myData.today_order_num,
						 			today_success_order_num: myData.today_success_order_num,
						 			today_price: myData.today_price,
						 			today_success_price: myData.today_success_price,
						 			today_income: myData.today_income,
						 			todo_price: myData.todo_price,
						 		}
						 		console.log(this.menus)
						 	}
						 }
					},  
				})
				
			} else {
				uni.showModal({
					title: '未登录',
					content: '您未登录，需要登录后才能继续',
					showCancel: !this.forcedLogin,
					success: (res) => {
						if (res.confirm) {
							if (this.forcedLogin) {
								uni.reLaunch({
									url: '../login/login'
								});
							} else {
								uni.navigateTo({
									url: '../login/login'
								});
							}
						}
					}
				});
			}	
			
 		}
 	}
